:root{
    --bgColor : rgba(236, 236, 236, 0.233);
}

.flex-utility {
    display: flex;
    gap: 1rem;
}

.checkout-main-container{
    justify-content: center;
    gap: 1rem;
    margin-top: 2%;
}

.checkout-main-container img{
    width: 100%;
}

.step-heading p{
    font-size: 1.1rem;
    font-weight: 500;
}

/* ////////// =============== Step 1 Css ==================== //////////// */

.review-order-wrapper{
    padding: 1%;
    background-color: var(--bgColor);
    flex-direction: column;
}

.close-icon{
    position: absolute;
    right: 0%;
    top: -3%;
    justify-content: flex-end;
    cursor: pointer;
}

.review-text-clip{
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.review-order-products{
    flex-direction: column;
    gap: 1.5rem;
}

.review-product-card-wrapper{
 position: relative;
 flex-wrap: wrap;
 padding: 2%;
 border-bottom: 1px solid rgba(179, 177, 177, 0.438);
}

.review-product-card-details{
    width: 40%;
    flex-direction: column;
    gap: 0.1rem;
}

.review-product-img{
    width: 30%;
    padding: 1%;
}

.review-product-price{
    justify-content: center;
    align-items: flex-end;
    width: 20%;
    padding: 1%;
}

/* ////////// =============== Step 1 Css Ends ==================== //////////// */

/* ////////// =============== Step 2 Css  ==================== //////////// */

.delivery-wrapper{
    background-color: var(--bgColor);
    padding: 1%;
    flex-direction: column;
}

.delivery-form{
    /* padding: 1%; */
    flex-direction: column;
}


/* ////////// =============== Step 2 Css Ends ==================== //////////// */

/* ////////// =============== Step 3 Select Pay  ==================== //////////// */

.payment-method-wrapper{
    background-color: white;
    flex-direction: column;
}

.pay-inner-wrapper{
    background-color: var(--bgColor);
    padding: 3%;
}

.order-summary-wrapper{
    background-color: var(--bgColor);
    padding: 3%;
}

.order-summary-product{
    justify-content: space-between;
}

.order-total{
    justify-content: space-between;
    padding: 3% 1%;
    background-color: rgba(192, 192, 192, 0.233);
}

/* ////////// =============== Step 3 Select Pay Ends ==================== //////////// */
